import React, { useState, useEffect } from 'react'
import {
  CCard,
  CCardHeader,
  CCardBody,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableRow,
  CButtonGroup,
  CButton,
  CTableHead,
  CTableHeaderCell
} from '@coreui/react'

import { useParams, useHistory } from 'react-router-dom'

import { Authentification } from '../../components/index'

import {
    getOptionLists,
    deleteOptionList
} from '../../provider/optionListFetch'

import {
  deleteOption
} from '../../provider/optionFetch'

import Loader from '../../components/Loader'
import Message from '../../components/Message'

const DetailOptionList = () => {

  /**Params Route */
  let { id } = useParams()
  let history = useHistory()
  const paramQuery = window.location.hash;
  const paramsJS = new URLSearchParams(
    paramQuery.substring(paramQuery.lastIndexOf("?"))
  );
  const restaurant = paramsJS.has("restaurant") ? paramsJS.get("restaurant") : null;


  /** State */
  const [optionList, setOptionList] = useState(null)
  const [error, setError] = useState(null)

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log('Run once, when the component mounts')

    async function fetch() {
      const resp = await getOptionLists(id)

      resp?.status ? setOptionList(resp?.data) : setError(resp?.error)
    }

    fetch()
  }, [id])

  const back = () => {
    history.push(`/detail-restaurant/${restaurant}`)
  }

  const ajout = () => {
    history.push(`/ajout-optionList`)
  }

  const editOptionListFunction = (idOptionList) => {
    history.push(`/ajout-optionList/${idOptionList}?restaurant=${restaurant}`)
  }

  const deleteOptionListFunction = async (idOptionList) => {
    const resp = await deleteOptionList(idOptionList)

    if (resp.status) {
      history.push(`/detail-restaurant/${restaurant}`)
    }
  }

  const editOptionFunction = (idOption, idOptionList) => {
    history.push(`/ajout-option/${idOption}?optionList=${idOptionList}`)
  }

  const deleteOptionFunction = async (idOption) => {
    const resp = await deleteOption(idOption)

    if (resp.status) {
      const optionsTMP = optionList.options.filter((u) => u._id !== idOption)

      setOptionList((old) => {
        return {...old, options: optionsTMP}
      })
    }
  }

  if (optionList === null) {
    return <Loader />
  }

  if (error) {
    return <Message> {error}</Message>
  }
  return (
    <>
        <Authentification />
        
      <CRow>
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardHeader>
              <CRow>
                <CCol sm={5}>
                  <strong>Option LIst {id}</strong>
                </CCol>
                <CCol sm={7}>
                  <CButtonGroup className="float-end me-3">
                    <CButton
                      color="outline-secondary"
                      className="mx-1"
                      onClick={() => back()}
                    >
                      Retour
                    </CButton>
                    <CButton
                      color="outline-danger"
                      className="mx-1"
                      onClick={() => deleteOptionListFunction(id)}
                    >
                      Suppprimer
                    </CButton>
                    <CButton
                      color="outline-secondary"
                      className="mx-0"
                      onClick={() => editOptionListFunction(id)}
                    >
                      Editer
                    </CButton>
                  </CButtonGroup>
                </CCol>
              </CRow>
            </CCardHeader>
            <CCardBody>
              <CTable>
                <CTableBody>
                  <CTableRow>
                    <CTableDataCell>Name</CTableDataCell>
                    <CTableDataCell>{optionList.name}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>Description</CTableDataCell>
                    <CTableDataCell>{optionList.description}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>type</CTableDataCell>
                    <CTableDataCell>{optionList.type}</CTableDataCell>
                  </CTableRow>
                </CTableBody>
              </CTable>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>

      <CRow>
          
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <CRow>
              <CCol sm={5}>
                <strong>Listes des Options</strong>
              </CCol>
              <CCol sm={7}>
                <CButtonGroup className="float-end me-3">
                  <CButton
                    color="outline-secondary"
                    className="mx-0"
                    onClick={() => ajout()}
                  >
                    Ajout
                  </CButton>
                </CButtonGroup>
              </CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
            <CTable>
              <CTableHead>
                <CTableRow>
                  <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Prix</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Actions</CTableHeaderCell>
                </CTableRow>
              </CTableHead>
              <CTableBody>
                {optionList?.options?.length > 0 ? (
                  optionList?.options?.map((o) => (
                    <CTableRow key={o._id}>
                      <CTableDataCell>{o.name}</CTableDataCell>
                      <CTableDataCell>{o.price}</CTableDataCell>
                      <CTableDataCell>
                        <CButton
                          color={'secondary'}
                          className="m-1"
                          onClick={() => editOptionFunction(o._id, optionList._id)}
                        >
                          {' '}
                          Editer{' '}
                        </CButton>
                        <CButton
                          color={'danger'}
                          onClick={() => deleteOptionFunction(o._id)}
                        >
                          {' '}
                          Supprimer{' '}
                        </CButton>
                      </CTableDataCell>
                    </CTableRow>
                  ))
                ) : (
                  <h3>{'NO Items'}</h3>
                )}
              </CTableBody>
            </CTable>
          </CCardBody>
        </CCard>
      </CCol>
      </CRow>
    </>
  )
}


export default DetailOptionList